@import url(https://fonts.googleapis.com/css2?family=Sarabun:wght@100;200;300;400;500;600;700&display=swap);
.box {
  background-image: linear-gradient(to right, #019a8f, #01bab3);
}

.homeBoxBtn {
  background-color: #fff;
  color: #d2c060;
}

.boxAction {
  background-color: #c7dad9;
  color: #d2c061;
}

.form-control {
  font-size: 1.5rem;
  color: #fff !important;
}

.form-control::-webkit-input-placeholder {
  color: #c7dad9;
}

.form-control:-ms-input-placeholder {
  color: #c7dad9;
}

.form-control::placeholder {
  color: #c7dad9;
}

#Main {
  height: 90vh;
}

#Main img {
  width: 25%;
}

.bounce {
  animation: Bounce 1s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes Bounce {
  0% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }

  100% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

@keyframes Bounce {
  0% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }

  100% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

.customBtn {
  background-color: #00bbb4 !important;
  color: #fff !important;
  padding: 1rem !important;
}

@media only screen and (max-width: 576px) {
  #Main img {
    width: 100%;
  }
}

#deliveryMain #deliveryMainContainer #upper img {
  height: 5rem;
  width: 5rem;
  -webkit-filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg)
    brightness(104%) contrast(97%);
          filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg)
    brightness(104%) contrast(97%);
}

#deliveryMain #deliveryMainContainer #upper h2 {
  font-weight: 300;
  line-height: 2.2rem;
}

iframe {
  max-width: 100%;
}

.nav-item .nav-link h2 img {
  height: 1.8rem;
  width: 1.8rem;
}

.JExFCTitle {
  color: #decc62;
}

#ErrorContainer {
  height: 89vh;
}

#ErrorContainer img {
  width: 50%;
}

@media only screen and (max-width: 576px) {
  #ErrorContainer img {
    width: 90%;
  }
}

.navbar-brand {
  background: -webkit-linear-gradient(#b8860b, #ecbd47);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

.navbar-brand img {
  height: 1.5rem;
  width: 1.5rem;
}

.activePage {
  color: #b8860b !important;
  background-color: #f2eee0;
  border-radius: 1.5rem;
}

.nav-item .nav-link {
  font-size: 1.05rem;
}

.nav-item .nav-link i {
  display: none;
}

.nav-item .nav-link.activePage i {
  display: inline-block;
}

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 0.5s ease-in-out;
  font-family: "Sarabun", sans-serif;
}

